import React, { Component } from 'react';
import { Route, Link, Redirect } from 'react-router-dom';
import axios from 'axios';

class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: false
        };
    }


    handleSignOut() {
        axios.post('/logout')
            .then(response => {
                if (response.status === 200) {
                    this.setState({
                        redirect: true
                    });
                }
            }).catch(error => {
                if (error.response){
                    //todo
                }
            });
    }

    render() {
        if (this.state.redirect) {
            return <Redirect to='/login' />
        }

        return (
            <div>
                <h1>Home</h1>

                <button onClick={this.handleSignOut.bind(this)}>Signout</button>

            </div>
        )
    }
}

export default Home;


