import React, { Component } from 'react';
import { Route, Link, Redirect, withRouter } from 'react-router-dom';
import axios from 'axios';

class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            username: '',
            password: '',
            redirect: false
        };

    }

    validateForm() {
        return this.state.username.length > 0 && this.state.password.length > 0;
    }

    handleChange(event) {
        this.setState({
            [event.target.id]: event.target.value
        });
    }

    handleSignIn(event) {
        event.preventDefault();

        const params = new URLSearchParams();
        params.append('username', this.state.username);
        params.append('password', this.state.password);

        axios.post('/authenticate', params)
            .then(response => {
                if (response.status === 200) {
                    this.setState({
                        redirect: true
                    });
                }
            }).catch(error => {
                if (error.response){
                    //todo
                }
            });
    }

    render() {
        if (this.state.redirect) {
            return <Redirect to='/home' />
        }

        return (
            <div>
                <h1>Feedit</h1>
                <form onSubmit={this.handleSignIn.bind(this)}>
                    <label htmlFor='username'>Username: </label>
                    <input type="text" name="username" id="username" value={this.state.username} onChange={this.handleChange.bind(this)} />

                    <label htmlFor="password">Password</label>
                    <input type="text" name="password" id="password" value={this.state.password} onChange={this.handleChange.bind(this)} />

                    <button type="submit" disabled={!this.validateForm()}>
                        Login
                    </button>
                </form>
            </div>
        )
    }
}

export default Login;


